<template>
    <div class=" main-contain">
        <mt-swipe class="lb-contain">
					<mt-swipe-item   v-for="(item ,index) in adData">
						<a :href=item.aSrc>
							<img :src=item.imgSrc alt="ad1" width="100%" height="100%">
						</a>
					</mt-swipe-item>
				</mt-swipe>
				<section>
				    <div class="index-zone">
				        <div class="title"><router-link :to="{path:'shopMore'}">热门商品</router-link></div>
                        <ul class="ul-box goods-list">
                                <li v-for="(item,index) in hotGoods">
                                    <div class="img-contain"><img :src="item.imgPath"></div><div><p>{{item.goodsName}}</p><p class="goods-price">￥{{item.price1}}</p></div>
                                </li>
                                <li class="tip" v-show="hotGoods.length==0">暂无热门商品</li>
                        </ul>
				    </div>
                   <div class="index-zone">
                      <div class="title"><router-link :to="{path:'shopMore'}">最新商品</router-link></div>
                      <ul class="ul-box goods-list">
                              <li v-for="(item,index) in newsGoods">
                                   <router-link :to="{path:'/shop/detail',query:{goodsCode:item.goodsCode,winNo:item.winNo,goodsName:item.goodsName}}">
                                  <div class="img-contain"><img :src="img1"></div><div><p>{{item.goodsName}}</p><p class="goods-price">￥{{item.price1}}</p></div>
                              </router-link>
                              </li>
                              <li class="tip" v-show="newsGoods.length==0">暂无最新商品</li>
                      </ul>
                  </div>
				</section>
    </div>
</template>
<script type="text/javascript">

  export default{
      data(){
        return {
                hotGoods:[{imgPath:require('assets/logo.png'),goodsName:"18K",price1:"11.11"},
                {imgPath:require('assets/logo.png'),goodsName:"古典味",price1:"11.11"},
                {imgPath:require('assets/logo.png'),goodsName:"养生",price1:"11.11"},
                {imgPath:require('assets/logo.png'),goodsName:"925银",price1:"11.11"},
                        ],
                newsGoods:[],
        }
      },
      created:function(){
            this.getNewsGoods();
      },
      computed:{
      adData:function(){
      		var img1=require('assets/2293.png');
      		var img2=require('assets/2300(1).png');
      		var img3=require('assets/2276.png');
      		return [{"imgSrc":img1,"aSrc":"#"},{"imgSrc":img2,"aSrc":"#"},{"imgSrc":img3,"aSrc":"#"}]
      },
      img1:function(){
          return require('assets/logo.png');
      }
  },
  methods:{
      getNewsGoods:function(){
          var pars={
              limit:4,
              offset:"0",
              winNo:"",
              goodsCode:"",
              goodsStatus:"5",
          }
          axiosUtil.upShopSellQry(pars).then(data=>{
              var data=data.data;
              this.newsGoods=data.rows;
          })
      }
  }

  }
</script>
<style media="screen" scope>
.main-contain{
height: 100%;
overflow: auto;
}
.lb-contain{
    height: 50vw;
}
    .index-zone{
        background: #fff;
        margin-top: 12px;
        padding-bottom: 6px;
    }
    .index-zone .title{
        font-size: 18px;
        text-align: center;
        color: #F17117;
        padding: 6px 0;
        border-bottom: 1px solid #eee;
    }
        .index-zone .img-contain{
            padding: 12px;
        }

    .index-zone .img-contain img{
        width: 100%;
        margin: 0 auto;
    }
    .goods-list{
        text-align: center;
    }
    .goods-price{
        color: #aaa;
        font-size: 12px;
    }
    .goods-list .tip{
        width: 100%;
        text-align: center;
        padding: 24px 0;
        color: #aaa;
    }
</style>



